@import url(https://fonts.googleapis.com/css?family=Comfortaa);

:root {
    --orange-color: #d18110;
    --white-color: #fafafa;
  }
body {
    font-family: 'Comfortaa';

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--white-color);
  box-sizing: border-box;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ingredients {
  width: 200px;
}

.wrapper {
  margin: 50px;
  padding: 20px;
  width: 100vh;

}
.shadow-drop-2-center {
  animation: shadow-drop-2-center 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

.difficulties {
font-style: italic;
font-size: 18px;
font-weight: lighter;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container img {
  width: 45%;
  border-radius: 15px;
}
.container h1 {
  color: var(--orange-color);
}
.wrapp-ingredient {
  border: 1px solid rgb(235, 235, 235);
  border-radius: 15px;
  margin-top: 10px;
  width: 200px;
  background-color: #f5f5f5;
}

.wrapper-ingredients {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  margin: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.instructions-list {
  display: flex;
  justify-content: center;
  text-align: center;
  list-style-position: inside;
  margin: 10px;
  padding: 10px;
}

.back-links-container {
  display: flex;
  justify-content: center;
  margin: 20px;
  grid-gap: 20px;
  gap: 20px;
}

.back-links {
  text-decoration: none;
  color: black;
  background-color: var(--white-color);
  border: none;
  border-radius: 10px;
  padding: 20px;
  font-size: 0.8em;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.15);
}

.back-links:hover {
  font-weight: bolder;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.45);
}

.instructions-list li {
  padding: 3px;
  font-size: 20px;
}

.coeur-vide, .coeur-plein {
  color: var(--orange-color);
}

.comment-section {
display: flex;
align-items: center;
justify-content: center;
}



