.list-favorites {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    /* margin: 25px; */
    grid-gap:40px;
    gap:40px;
    text-align: center;
    
}

.link-recipe {
    text-decoration: none;
    color: var(--orange-color);
    font-size: 22px;
    /* font-weight: bold; */
}

.link-recipe:hover {
   
    font-weight: bolder;
}
.form-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
  width:400px;
  height: 650px;
  padding: 30px;
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  color: var(--orange-color);
  position: relative;
  left: 180px;
}
.error {
  /* background-color: var(--orange-color); */
  color: var(--orange-color);
  font-weight: bolder;
}
.form-wrap input {  
  border: none;
  border-radius: 15px;
  width: 300px;
  box-shadow: 0 0 10px 0px rgb(0 0 0 / 50%);
  
  color: var(--orange-color);
  padding: 10px;
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--orange-color);
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--orange-color);
}

.form-wrap select {
  border: none;
  box-shadow: 0 0 10px 0px rgb(0 0 0 / 50%);
  height: 60px;
  color: var(--orange-color);
  border-radius: 15px;
}

.bounce-in-top {
  animation: bounce-in-top 1.1s both;
}

.general-wrap {
  display: flex;
  justify-content: space-between; 
  grid-gap: 200px; 
  gap: 200px;
  align-items: flex-start;
  margin: 80px;
  
}

.title {
  color: var(--orange-color);
  margin-top: 100px;
  margin-top: 30px;
}

.info-box {
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  width: auto;
  height: auto;
  padding: 25px;
  color: var(--orange-color);
  position: relative;
  top: 163px;
  right: 122px;

}

.table-title {
  display: flex;
  flex-direction: row;
  position: relative;
  right: 30px;
}

.table-title th {
  margin: 15px;
}

.table-title td {
  margin: 15px;
}

.table {
  margin: auto;
  display: flex;
  justify-content: space-around;
}

.ingredients {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
  
}

.ingredients-creation{
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  height: 270px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  width: 360px;
  padding: 15px;

}

.create-button{
  width: 150px;
  height: 25px;  
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  
}

.create-button:hover{
  background-color: var(--orange-color) ;
  color: white;
}

.instructions-recipe{
  padding: 10px;
  height: 200px;
  width: 360px;
  border-radius: 15px;
}

.input-file{
  width: 280px;
  height: 53px;
  
}
